@use "../utility/" as ut;

header {
  @include ut.flex(column, space-between, center, 2rem);
  padding: 9rem;
  background: linear-gradient(
    rgba($color: ut.$main-1, $alpha: 0.1),
    rgba($color: ut.$neutral-3, $alpha: 0.5)
  );
  animation: header_anim 1s ease-in-out;

  h1 {
    font-size: 3rem;
    font-weight: 300;
    letter-spacing: 0.1rem;
  }

  ul {
    @include ut.flex(row, space-between, center, 3rem);
    list-style-type: none;
    font-weight: 700;
    li {
      a {
        text-decoration: none;
        color: ut.$neutral-1;
        &:hover {
          color: ut.$main-1;
          transition: all 0.3s ease-in;
        }
      }
    }
  }
}

@keyframes header_anim {
  from {
    filter: grayscale(100%);
    opacity: 0.2;
  }
  to {
    filter: grayscale(0%);
    opacity: 1;
  }
}
