header {
  background: linear-gradient(#00ffcd1a, #21212180);
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  padding: 9rem;
  animation: header_anim 1s ease-in-out;
  display: flex;
}

header h1 {
  letter-spacing: .1rem;
  font-size: 3rem;
  font-weight: 300;
}

header ul {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
  font-weight: 700;
  list-style-type: none;
  display: flex;
}

header ul li a {
  color: #fff;
  text-decoration: none;
}

header ul li a:hover {
  color: #00ffcd;
  transition: all .3s ease-in;
}

@keyframes header_anim {
  from {
    filter: grayscale();
    opacity: .2;
  }

  to {
    filter: grayscale(0%);
    opacity: 1;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

img {
  max-width: 100%;
  display: block;
}

body {
  color: #fff;
  background-color: #212121;
  font-family: Raleway, sans-serif;
  font-size: 1rem;
}

